import { styled } from '@mui/system';
import useMediaQuery from '@mui/material/useMediaQuery';

import logo from './logo2.svg';
import map from './map.png';

export default function Where() {
  const isDesktop = useMediaQuery('(min-width:600px)');
  const StyledComponent = styled('div')({
    textAlign: 'center',
    marginBottom: '300px',
  });
  const scale = isDesktop ? 0.5 : 1;
  return (
    <section id="where">
      <StyledComponent>
        <div>
          <img id="bgLogo" src={logo} alt="" style={{
            position: 'absolute',
            transform: `rotate(15deg) scale(${scale})`,
            opacity: 0.1,
          }} />
        </div>
        <a href="https://naver.me/5rsMBlxb" target="_blank" rel="noreferrer">
          <img src={map} alt="map" style={{
            height: '100vmin',
            width: '100%',
            objectFit: 'none',
            objectPosition: '55% 50%',
          }}/>
        </a>
      </StyledComponent>
    </section>
  );
}